import React from 'react'
import { Link } from "react-router-dom"
import { Helmet } from "react-helmet"
import { Footer } from '../Footer/Footer'

export const guiamentorias = () => {
    return (
        <>
            <Helmet>
                <meta charSet="utf-8" />
                <title>Gua para participar en el programa de mentoras - FrontendCaf</title>
            </Helmet>

            <div className="sm:pt-10 pb-24 bg-indigo-100">
                <div className="flex flex-col mx-auto w-9/12 bg-white rounded-xl shadow-xl">
                    <div className="p-5 border-b border-gray-200">
                        <h1 className="text-2xl font-semibold text-green-500">Gua para participar en el programa de mentoras</h1>
                    </div>
                    <div className="ml-10 mr-10 mt-6 tracking-tight">
                        <h1 className="text-2xl text-green-500 mb-2">Dnde se realizan las mentoras?</h1>
                        <ul className="text-gray-700 leading-7">
                            <li><b>Las mentoras se realizan dentro del Discord de FrontendCaf, si no has registrado en dicha plataforma, la mentora no se llevar a cabo.</b></li>
                            <li>El canal para realizar la mentora ser visible para el mentoreado en el da y horario de la mentora, cualquier consulta puedes realizarla en el canal llamado <b>#consultas-mentorias</b></li>
                        </ul>
                        <div>
                            <h1 className="text-2xl font-normal text-green-500 mb-2 mt-2">Requisitos para participar</h1>
                            <ul className="text-gray-700 leading-7">
                                <li><b>Brindar datos de contacto</b> como Linkedin, nombre, apellido. No podr haber participantes annimos en ninguna de las partes.</li>
                                <li><b>Aceptar y cumplir el cdigo de conducta de <Link to="/docs/codigo-de-conducta" className="text-green-500 underline">FrontendCaf</Link>.</b></li>
                            </ul>
                        </div>
                        <div>
                            <h1 className="text-2xl font-normal text-green-500 mb-2 mt-2">IMPORTANTE</h1>
                            <p className="text-gray-700"><b>Comprometerse a asistir.</b></p>
                            <ul className="text-gray-700 leading-7">
                                <li>En caso de no poder, comunicate con la mayor anticipacin posible. Si no se puede comunicar de antemano, deber justificarse la ausencia.</li>
                                <li>Una (1) falta sin justificar finaliza el proceso de mentora.</li>
                                <li>Revisa tema de Non-Disclosure Agreement en tu trabajos a la hora de compartir cdigo.</li>
                            </ul>
                            <div>
                                <h1 className="text-2xl font-normal text-green-500 mb-2 mt-2">Antes de la mentora debes:</h1>
                                <ul className="text-gray-700 leading-7">
                                    <li><b>Preparar dudas con anticipacin.</b></li>
                                    <li>Si la mentora implica cdigo, prepara el mismo en un editor colaborativo como codepen, o un repositorio actualizado y listo para ser compartido.</li>
                                </ul>
                            </div>
                            <div>
                                <div className="mb-10">
                                    <h1 className="text-2xl font-normal text-green-500 mb-2 mt-2">Aclaraciones</h1>
                                    <ul className="text-gray-700 leading-7">
                                        <li><b>Las mentoras</b> que se dan dentro de FrontendCaf son de <b>carcter gratuito</b>.</li>
                                        <li><b>No se requiere ningn nivel para anotarse</b>, puede anotarse alguien que no sabe nada, o alguien que sabe un poco y necesita ayuda en algo particular.</li>
                                        <li><b>El alcance de las mentoras va a variar</b>, dependiendo del tema, la idea es que sea sesiones nicas, o dos sesiones.</li>
                                        <li>No estamos ofreciendo mentoras extendidas en el tiempo, la idea es ayudarte a destrabar un problema si es cdigo, o a hacer el cv, y ver cosas puntuales referidas a cada tiempo, las mentoras no son clases ni cursos.</li>
                                        <li>Puedes anotarse en ms de una categora.</li>
                                        <li><b>FrontendCaf no se hace responsable de cualquier uso, comunicacin y/o accin que se realice fuera de las salas asignadas.</b></li>
                                        <li>Queda prohibido el ofrecimiento de servicios que se encuentre por fuera de los trminos y condiciones explicitados en el presente documento -ya sean iguales o similares al presente- en nombre de FrontendCaf.</li>
                                    </ul>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
}

